<template>
    <div>
        <v-dialog v-model="dialog">
            <v-card v-if="dialog && chartData != null">
                <div class="bannerStyle text-center">
                    {{ name }}
                </div>
                <v-divider></v-divider>
                <VeLine :data="chartData" ></VeLine>

            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import VeLine from 'v-charts/lib/line.common'  //
import VeHistogram from 'v-charts/lib/histogram.common'  //

export default {
    components:{ VeLine, VeHistogram },
    data(){
        return {
            dialog:false,
            items:null,
            chartData:null,

            name:"",
        }
    },

    methods:{
        //
        showMultiProcess(items){
            this.items = items;
            this.dialog = true;
            this.$nextTick(()=>{
                let dataItems = []
                let colums = ['']
                //
                for (let i=1;i<=12;i++){
                    dataItems.push({
                        '': `${i}`
                    })
                }

                items.forEach(item=>{
                    colums.push(item.name)
                    item.monthData.forEach((mItem,index)=>{
                        dataItems[index][item.name] = mItem.subTotal
                    })
                })
                
                this.name = ': ' + colums.slice(1)
                this.chartData = {
                    columns: colums,
                    rows: dataItems
                }
            })
        },
        //
        showProcess(items){
            this.items = items;
            this.name = items.name;
            this.dialog = true;
            this.$nextTick(()=>{
                this.chartData = {
                    columns: ["", ""],
                    rows: items.monthData.map((item,index)=>{
                        return {
                            '' : `${index+1}` ,
                            '' : item.subTotal,
                        }
                    })
                }
            })

            
        }
    }
}
</script>